/** UI **/
.no-webp .inicio {
  background-image: url(../img/portada.jpg);
}

.webp .inicio {
  background-image: url(../img/portada.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 50rem;
  }
}
.header h1:hover {
  color: #71B100;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #71B100;
}

.footer {
  padding: 5rem, 2 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 5rem, 2;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    padding: 35px 0;
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.tabla-precio {
  border: 1px solid #e1e1e1;
  background-color: #ffffff;
  padding: 40px 0;
  text-align: center;
  transition: all 0.3s ease;
}

.tabla-precio:hover {
  transform: scale(1.1);
  border: 1px solid #fe4918;
  box-shadow: 0px 0px 50px #666;
}

.mapa {
  height: 420px;
}

.carrusel {
  padding: 3rem;
}

.carrusel img {
  max-height: 565px;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]) {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario input[type=checkbox] {
  margin: 0;
  width: auto;
}

#fondologin {
  height: auto;
  width: 85%;
}

@media (min-width: 768px) {
  .contenedor-citas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 90vh;
  }
}
.contenedor-citas .imagen {
  background-image: url(../img/test.jpg);
  background-size: cover;
  background-position: center center;
  height: 20rem;
}
@media (min-width: 768px) {
  .contenedor-citas .imagen {
    height: auto;
    width: auto;
  }
}
.contenedor-citas .app {
  width: 95%;
  margin: 0 auto;
}

.listado-servicios p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.listado-servicios label {
  font-weight: 700;
  display: block;
}
.listado-servicios input:not([type=submit]) {
  padding: 1rem;
  display: block;
  width: 95%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

@media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px) {
  table.ConsultaTabla {
    /* Force table to not be like tables anymore */
    /* Hide table headers (but not display: none;, for accessibility) */
  }
  table.ConsultaTabla table, table.ConsultaTabla thead, table.ConsultaTabla tbody, table.ConsultaTabla th, table.ConsultaTabla td, table.ConsultaTabla tr {
    display: block;
  }
  table.ConsultaTabla thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  table.ConsultaTabla tr {
    margin: 0 0 1rem 0;
  }
  table.ConsultaTabla tr:nth-child(odd) {
    background: #ccc;
  }
  table.ConsultaTabla td {
    /* Behave  like a "row" */
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }
  table.ConsultaTabla td:before {
    /* Now like a table header */
    position: absolute;
    /* Top/left values mimic padding */
    top: 0;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    font-weight: bold;
    text-shadow: 0.5px 0.5px 0.5px #1c88ee;
  }
  table.ConsultaTabla td:nth-of-type(1):before {
    content: "Servicio";
  }
  table.ConsultaTabla td:nth-of-type(2):before {
    content: "Fecha:";
  }
  table.ConsultaTabla td:nth-of-type(3):before {
    content: "Hora Inicio:";
  }
  table.ConsultaTabla td:nth-of-type(4):before {
    content: "Hora Final:";
  }
}

